@mixin typo-developer {
  background-color: lighten($color-primary, 23%);
  padding: 10px 15px;
  margin-top: 10px;
  margin-bottom: 5px;
  overflow-x: auto;
  font-family: monospace;
  position: relative;
  color: $color-text;
  text-align: left;
  &:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: lighten($color-primary, 10%);
    width: 4px;
  }
  strong {
    font-weight: 600;
  }
}

@mixin typo-quote {
  position: relative;
  padding: 5px 15px;
  margin-top: 5px;
  margin-bottom: 10px;
  background-color: lighten($color-primary, 96%);
  &:before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: lighten($color-primary, 80%);
    width: 4px;
  }
}
