$navbar-break-point: $small;

// CSS variables
:root {
  --padding-mobile: 15px;

  --padding-tiny: .4vw;
  --padding-small: .8vw;
  --padding-medium: 1.6vw;
  --padding-large: 3.7vw;


  --padding-sides-small: var(--padding-small);
  --padding-sides-medium: var(--padding-medium);
  --padding-sides-large: var(--padding-large);

  --section-padding-top: var(--padding-large);
  --section-padding-bottom: calc(var(--padding-large) * 1.4 );



  --margin-tiny: .8vw;
  --margin-small: 1.2vw;

  --content-width: 82.5vw;

  --product-padding-sides: 8vw;

  @media (max-width: $small) {
    --padding-small: 10px;
    --padding-medium: 20px;
    --padding-large: 30px;

    --padding-sides-small: 15px;
    --padding-sides-medium: 15px;
    --padding-sides-large: 15px;
  }
  @media (max-width: $xsmall) {
    --content-width: calc(96vw - var(--padding-mobile)*2);
  }

}
