.ico {
  --size: 16px;
  --width: var(--size);
  --height: var(--size);
  width: var(--width);
  height: var(--height);

  display: inline-block;

  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  &-developer {
    --size: 16px;
    background-image: url(#{$base64-icon-developer});
  }
  &-phone,
  &-envelope,
  &-phone-alt,
  &-envelope-alt,
  &-clock-alt {
    --width: 11vw;
    --height: 7vw;
    @media (max-width: $small) {
      --width: 70px;
      --height: 50px;
    }
  }
  &-phone {
    background-image: url(#{$base64-icon-phone});
  }
  &-envelope {
    background-image: url(#{$base64-icon-envelope});
  }
  &-phone-alt {
    background-image: url(#{$base64-icon-phone-alt});
  }
  &-envelope-alt {
    background-image: url(#{$base64-icon-envelope-alt});
  }
  &-clock-alt {
    background-image: url(#{$base64-icon-clock-alt});
  }
  &-cart,
  &-search {
    --size: 1.2vw;
  }
  &-cart {
    background-image: url(#{$base64-icon-cart});
  }
  &-search {
    background-image: url(#{$base64-icon-search});
  }
  &-pinterest,
  &-instagram,
  &-twitter,
  &-facebook {
    --size: 1.8em;
    background-image: url(#{$base64-icon-facebook});
  }
  &-pinterest {
    background-image: url(#{$base64-icon-pinterest});
  }
  &-instagram {
    background-image: url(#{$base64-icon-instagram});
  }
  &-twitter {
    background-image: url(#{$base64-icon-twitter});
  }
  &-facebook {
    background-image: url(#{$base64-icon-facebook});
  }
  &-link-arrow {
    background-image: url(#{$base64-icon-link-arrow});
  }
  &-cake {
    --size: 5vw;
    background-image: url(#{$base64-icon-cake});
    @media (max-width: $xsmall) {
      --size: 40px;
    }
  }
  &-gift {
    --size: 5vw;
    background-image: url(#{$base64-icon-gift});
    @media (max-width: $xsmall) {
      --size: 40px;
    }
  }
  &-shop {
    --size: 5vw;
    background-image: url(#{$base64-icon-shop});
    @media (max-width: $xsmall) {
      --size: 40px;
    }
  }
  &-next,
  &-prev {
    --size: 2vw;
  }
  &-prev {
    background-image: url(#{$base64-icon-prev});
  }
  &-next {
    background-image: url(#{$base64-icon-next});
  }
  
  &-expand {
    --size: 16px;
    background-image: url(#{$base64-icon-expand});
  }

}
