.btn {
  height: var(--btn-height);
  appearance: none;
  display: inline-block;
  white-space: nowrap;
  cursor: pointer;
  padding: 1vw 2.2vw;
  border: 0;
  border-radius: 50px;
  text-align: center;
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 1;
  font-family: $secondary-font-face;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  &:focus {
    outline: 0;
  }
  @media (max-width: $xsmall) {
    padding: 8px 12px;
  }
  &-big {
    font-size: 1.6rem;
    padding: calc(var(--padding-small) * 1.4) calc(var(--padding-small) * 5);
  }
  &-small {
    font-size: .9rem;
    padding: calc(var(--padding-small) * .6) calc(var(--padding-small) * 1.2);
  }
  &-primary {
    background-color: $color-primary;
    color: $color-white;
    border: 1px solid $color-primary;
    &:hover {
      @extend .btn-primary-alt;
    }
  }
  &-green {
    background-color: $color-green;
    color: $color-white;
    border: 1px solid $color-green;
    &:hover {
      border: 1px solid $color-green;
      background-color: transparent;
      color: $color-green;
    }
  }
  &-cart {
    background-color: $color-lred;
    color: $color-white;
    border: 1px solid $color-lred;
    text-transform: uppercase;
    font-size: 1.9em;
    font-weight: 600;
    padding: 1.2vw 2vw;
    @media (max-width: $small) {
      padding: 10px 20px;
    }
    &:hover {
      background-color: transparent;
      color: $color-lred;
    }
  }
  &-primary-alt {
    color: $color-primary;
    background: none;
    border: 1px solid $color-primary;
    &:hover {
      @extend .btn-primary;
    }
  }
  &-white {
    color: $color-primary;
    background-color: $color-white;
    border: 1px solid $color-white;
    &:hover {
      @extend .btn-primary;
    }
  }
}
