/*
  Typography settings
*/
html {
	font-smoothing: antialiased;
	-webkit-font-smoothing: antialiased;
	font-size: responsive-value(10px, 400);
	font-family: $base-font-face;
	@media (max-width: $small) {
		font-size: 12px;
	}
}
body {
}

pre,
.debug {
	@include typo-developer;
}

#tinymce,
.typo {
	color: $color-text;
	line-height: 1.4;
	.success {
		color: $color-success;
	}
	.error {
		color: $color-error;
	}

	span {
		color: $color-primary;
	}

	&.alt {
		p,
		h1, h2, h3, h4, h5, h6,
		li {
			color: $color-text-alt;
			span {
				// color: $color-black;
			}
		}
		hr {
			background-color: $color-text-alt;
		}
	}

	h1, h2, h3, h4, h5, h6,
	.h1, .h2, .h3, .h4, .h5, .h6 {
		margin-top: .1vw;
		margin-bottom: .3vw;
		line-height: 1.3;
		letter-spacing: .04vw;
		font-family: $secondary-font-face;
	}

	h1, h2.h1 {
		font-size: 2.2em;
		font-weight: 400;
		letter-spacing: 1px;
		line-height: 1.1;


		@media (max-width: $xsmall) {
			font-size: 2.2em;
		}
		span {
			font-weight: 700;
		}
		a {
			font-weight: 800;
		}
	}
	h2, p.h2 {
		font-size: 2.25em;
		font-weight: 400;
		@media (max-width: $xsmall) {
			font-size: 1.8em;
		}
	}
	h3, p.h3 {
		font-size: 1.8em;
	}
	h4 {
		font-size: 1.1em;
		font-weight: 500;
	}
	h5 {
		font-size: 1em;
		font-weight: 300;
	}
	h6 {
		font-size: .9em;
	}

	p {
		font-size: 1.2em;
		line-height: 1.3;
		margin-bottom: .7vw;
		font-weight: 400;
		display: block;
		&.lead {
			font-weight: 700;
			font-size: 1.2em;
			line-height: 1.2;
			font-family: $secondary-font-face;
		}
		span {
			color: $color-red;
		}
	}

	a:not(.btn) {
		// font-weight: 700;
		display: inline-block;
		color: $color-primary;
	}

	blockquote,
	q {
		@include typo-quote;
	}
	q {
		display: inline-block;
		padding: 10px 15px;
	}

	ul,
	ol {
		margin-top: 5px;
		margin-bottom: 15px;
	}

	li {
		font-weight: 500;
		margin-bottom: .4em;
		// font-size: 1.1em;
		@media (max-width: $small) {
			font-size: 1rem;
		}
	}

	// .typo unordered list
	ul {
		> li {
			padding-left: 20px;
			// padding-left: 30px;
			position: relative;
			display: block;
			--bullet-size: .45em;
			&:before {
				content: '';
				position: absolute;
				display: block;
				left: .4em;
				top: .5em;
				width: var(--bullet-size);
				height: var(--bullet-size);
				border-radius: 50%;
				background-color: $typo-ul-color;
				// background: linear-gradient(180deg, $color-primary, $color-secondary);
				background-color: $color-primary;
			}
		}
	}
	// .typo ordered list
	ol {
		list-style: none;
		counter-reset: li;

		> li {
			padding-left: 20px;
			position: relative;
			display: block;

			&:before {
				content: counter(li) ".";
				counter-increment: li;
				display: block;
				position: absolute;
				top: 0;
				left: -24px;
				width: 40px;
				font-weight: 700;
				color: $typo-ol-color;
				text-align: right;
			}
		}
	}
	// .typo definition list
	dl {
		dt {
		}

		dd {
			margin-left: 15px;
		}
	}

	strong {
		font-weight: 700;
	}

	sub {
		font-size: 0.8em;
		position: relative;
		top: 0.3em;
	}

	sup {
		font-size: 0.8em;
		position: relative;
		bottom: 0.3em;
	}

	small {
		font-size: 0.7em;
	}

	hr {
		display: inline-block;
		height: .22vw;
		max-width: 2.4vw;
		width: 100%;
		border: 0;
		margin: 0;
		margin-top: var(--margin-tiny);
		margin-bottom: var(--margin-tiny);
		background-color: $color-primary;
	}

	img {
		display: block;
	}

	table {
		// border: 1px solid lighten($color-text, 20%);
		min-width: 100%;
		overflow-x: auto;
		font-family: $secondary-font-face;
		thead {
		}
		tbody {
			tr {
				&:last-child {
					border: 0;
				}
			}
		}
		tr {
			border-bottom: 1px solid lighten($color-text, 20%);
			th,
			td {
				// border: 1px solid lighten($color-text, 20%);
				padding: 10px 5px;
				text-align: center;
				vertical-align: middle;
			}
			th {
				font-weight: 500;
				color: $color-text;
				border-radius: 6px;
			}
			td {
				color: lighten($color-text, 30%);
			}
		}
	}

	&--featured {
		font-size: 25px;
	}
}

.text {
	&--blue {
		color: $color-primary;
	}
}
